/**
 * Endpoints that allow to communicate with `cmd` as a server that uses the OpenAI completion API.
 */

import { Request, Response, Router } from "express"
import { UserFacingError } from "../errors"
import { Model } from "openai/resources/models"
import { PageResponse } from "openai/pagination"
import { ChatCompletionChunk, ChatCompletionCreateParamsStreaming } from "openai/resources/index"
import { logInfo } from "@/logger"

export const registerEndpoints = (router: Router) => {
	// Like https://api.openai.com/v1/models
	router.post("/v1/models", async (req: Request, res: Response) => {
		logInfo(`Received request to list models: ${JSON.stringify(req.body, null, 2)}`)

		const response: PageResponse<Model> = {
			object: "list",
			data: [
				{
					id: "claude-code",
					object: "model",
					created: 1686588896,
					owned_by: "anthropic",
				},
			],
		}
		res.json({ response })
	})
	// Like https://api.openai.com/v1/chat/completions
	router.post("/v1/chat/completions", async (req: Request, res: Response) => {
		logInfo(`Received request complete chat: ${JSON.stringify(req.body, null, 2)}`)
		const body = req.body as ChatCompletionCreateParamsStreaming

		res.setHeader("Content-Type", "text/event-stream")
		res.setHeader("Cache-Control", "no-cache")
		res.setHeader("Connection", "keep-alive")
		const completionId = `chatcmpl-${Math.random().toString(36).substring(2, 15)}`
		const createdAt = Math.floor(Date.now() / 1000)

		const sendChunk = (chunk: ChatCompletionChunk) => {
			res.write(`data: ${JSON.stringify(chunk)}\n\n`)
		}

		sendChunk({
			id: completionId,
			object: "chat.completion.chunk",
			created: createdAt,
			model: body.model,
			choices: [{ index: 0, delta: { role: "assistant", content: "", refusal: null }, finish_reason: null }],
		})

		sendChunk({
			id: completionId,
			object: "chat.completion.chunk",
			created: createdAt,
			model: body.model,
			choices: [{ index: 0, delta: { role: "assistant", content: "hi!", refusal: null }, finish_reason: null }],
		})

		sendChunk({
			id: completionId,
			object: "chat.completion.chunk",
			created: createdAt,
			model: body.model,
			choices: [{ index: 0, delta: {}, finish_reason: "stop" }],
		})
		res.write("data: [DONE]")
		res.end()
	})
}
